import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'src/environments/environment';
import { Employee } from '../model/employee';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  // private baseUrl: string = "http://localhost:8080/"
  private baseUrl = environment.baseUrl;
  private empObj:Employee;

  constructor(private http: HttpClient, private dialog: MatDialog) { }

  login(obj) {
    return this.http.post(`${this.baseUrl}login`, obj);
  }

  getAllEmployee() {
    return this.http.get<Employee[]>(`${this.baseUrl}employee/getAllEmployee`)
  }

  getEmployeeOnId(eid) {
    return this.http.get(`${this.baseUrl}employee/getParticularEmployee`, {
      params: {
        id: eid
      }
    })
  }

  getAllCountries() {
    return this.http.get(`${this.baseUrl}country/getAllCountry`);
  }

  addEmployee(obj){
    return (this.http.post(`${this.baseUrl}employee/addEmployee`,obj,{
      responseType:'text'
    }));
  }

  updateEmployee(obj){
    return this.http.put(`${this.baseUrl}employee/updateEmployee`,obj,{
      responseType:'text'
    })
  }

  deleteEmployee(empId){
    return this.http.delete(`${this.baseUrl}employee/deleteEmployee`,{
      responseType: 'text',
      params:{
       'id': empId 
      }
    })
  }

  setEmpObj(obj){
    this.empObj = obj;
  }

  getEmpObj(){
    return this.empObj;
  }

}
